import React from 'react'
import { useCMS } from '../contexts/CMSContext'
import './nien-khoa.css'

const NienKhoaPage: React.FC = () => {
  const { displayData } = useCMS()
  const { header, nienKhoaContent } = displayData.pages.page6

  return (
    <div className="nien-khoa-page">
      {/* Header Section */}
      <header className="page-header" style={{ backgroundImage: `url(${header.backgroundImage})` }}>
        <div className="header-content">
          <h1>{header.title}</h1>
          <h3>{header.mobileTitle}</h3>
        </div>
      </header>

      {/* Content Blocks */}
      <main className="nien-khoa-content">
        {nienKhoaContent.blocks.map((block: any) => (
          <section key={block.id} className={`block block-${block.type}`}>
            <div className="block-header">
              <h2>{block.title}</h2>
              <img
                src={block.imageDesktop}
                alt={block.title}
                className="block-image desktop"
              />
              {block.imageMobile && (
                <img
                  src={block.imageMobile}
                  alt={block.title}
                  className="block-image mobile"
                />
              )}
            </div>

            {block.type === 'schedule' && block.schedules && (
              <div className="schedule-section">
                <h3>{block.schedules.heading}</h3>
                <div className="schedule-grid">
                  {block.schedules.items.map((schedule: any) => (
                    <div key={schedule.id} className="schedule-item">
                      <h4>{schedule.dayLabel}</h4>
                      <ul>
                        {schedule.details.map((detail: string, index: number) => (
                          <li key={index}>{detail}</li>
                        ))}
                      </ul>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {block.type === 'course' && block.courses && (
              <div className="courses-section">
                <div className="courses-grid">
                  {block.courses.map((course: any) => (
                    <div key={course.id} className="course-card">
                      <h3>{course.title}</h3>
                      <p>{course.description}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </section>
        ))}
      </main>
    </div>
  )
}

export default NienKhoaPage